@font-face {
    font-family: 'Quicksand-Regular';
    src: url('../assets/fonts/Quicksand-Regular.ttf');
}

@font-face {
    font-family: 'Quicksand-Bold';
    src: url('../assets/fonts/Quicksand-Bold.ttf');
}

body {
    background-color: #10103A;
    color: #fff;
    font-family: 'Quicksand-Regular';
    font-weight: 400;
    margin: 0;
    text-align: center;
}

h1 {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Quicksand-Bold';
}

h4 {
    font-family: 'Quicksand-Regular';
    font-weight: 400;
}

.container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
}

.header {
    width: 100%;
}

.logo {
    width: 50px;
}

.forecastContainer {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
}